.profile-img{
    &__container{
        width: 15rem;
        height: 15rem;
        margin: auto;
        margin-bottom: 2.5rem;
        .img{
            width: 100%;
            height: 100%;
            border-radius: 50%;
            box-shadow: 5px -4.5px 0 2px blue;
        }
    }
}